<template>
  <div class="post-content">
    <h5>
      {{post.author}}
    </h5>
    <p>
      {{post.post_date}}
    </p>
    <p>{{post.text}}</p>
  </div>
  <div class="post-comment" v-for="item in commentList" :key="item">
    <h5>{{item.author}}</h5>
    <p>{{item.postdate}}</p>
    <p>{{item.text}}</p>
  </div>
  <div>
    <form @submit.prevent>
      <input v-model="text">
      <button @click="postComment(post.post_id)">comment</button>
    </form>
    
  </div>
</template>

<script>
import axios from 'axios';
import { mapActions, mapState } from 'vuex'

export default {
  computed:{...mapState(['postList','commentList','todayDate'])},
  created(){
    this.post = this.postList.find(this.findPost);
    this.getCommentList(this.post.post_id)

  },
  methods:{
    ...mapActions(['getCommentList']),
    findPost(element){
      if(element.post_id == this.$route.params.id){
        return true;
      }
    },
    postComment(id){
      axios.post(`/api/community/comment/${id}`,{
        text:this.text,
        postDate:this.todayDate
      })
      .then(()=>{
        this.getCommentList(id);
      })
      .catch((err)=>{
        console.error(err);
      })
    },
  },
  data(){
    return{
      post:null,
      text:null,
    }
  }
}
</script>

