<template>

<WritePost v-if="postModal==true" @postRegister="closePostModal();getPostList()"/>


  <div class="post" v-for="(item) in postList" :key="item" @click="$router.push(`/detail/${item.post_id}`)">
    <div class="post-head">
        <h5>{{item.author}}</h5>
        <p>{{item.post_date}}</p>
    </div>
    <div class="post-content">
        <p>{{item.text}}</p>
    </div>
  </div>
  <button @click="openPostModal()">new Post</button>
</template>

<script>
import WritePost from '@/components/WritePost.vue'

import { mapActions, mapMutations, mapState } from 'vuex'

export default {
  components: { WritePost },

    computed:{
        ...mapState(['postList','postModal'])
    },
    methods:{
        ...mapMutations(['openPostModal','closePostModal']),
        ...mapActions(['getPostList',])
    }
}

</script>

<style>
.post{
  border: 1px solid #000;
}
</style>